.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 166px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  height: 134px;
  margin: 0 3px 40px;
}

.card__name {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  text-align: center;
  margin: 0;
  padding-top: 1px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-name {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-count {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  display: flex;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 > :not(:last-child) {
  background-image: url("star-active.edda4c43.svg");
  margin-right: 4px;
}

.stars--4 > :last-child {
  background-image: url("star.822d561e.svg");
}

.buy {
  text-transform: uppercase;
  color: #fff;
  box-sizing: border-box;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-block: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.buy:hover {
  color: #00acdc;
  background: none;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

